.App {
  text-align: center;
  padding: 0px;
  background-color: #f0ede9;
}

.main-images {
  border: 15px solid  #f0ede9;
  padding: 0%;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4), 0 8px 22px 0 rgba(0, 0, 0, 0.23);
  
}



.header-image {
  margin-bottom: 50px;
  width: 100%;
  height: 350px;
}

ion-icon {
  font-size: xx-large;
  border-radius: 100px;
  margin-left: 80px;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4), 0 8px 22px 0 rgba(0, 0, 0, 0.23);
  padding: 0%;
}

.container {
  position: relative;
  text-align: center;
  color: white;
}

.centered {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navbar-brand {
  font-family: "Fugaz One", cursive;
  text-align: center;
  font-size: xx-large;
  margin: auto;
  
  
}

.title {
  font-family: "Fugaz One", cursive;
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90vh;
  letter-spacing: 1px;
  
}

h1 {
  background-image: url(https://media3.giphy.com/media/fwo7bzEVxbYS4eSNVd/giphy.gif?cid=ecf05e47q79nl41cylfnlxkre253enej4heotwxawa52f7w1&rid=giphy.gif);
  background-size: cover;
  color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  font-size: 95px;
  line-height: 1;
  margin: 10px 0;
  -moz-box-shadow: inset 0 0 20px #ec2a2a;
  -webkit-box-shadow: inset 0 0 20px #db5050;
  box-shadow: inset 0 0 20px #5f5d5d;
  
}

#navbar {
  background: rgb(226, 218, 218) ; 
  margin-left: 8px;
  margin-right: 8px;
  padding: 30px;
  text-align: center;
  border: 2px solid whitesmoke;

}


#footer {
  background: conic-gradient(
    rgb(248, 186, 186),
    rgb(250, 250, 179),
    rgb(180, 248, 180),
    rgb(162, 162, 247),
    rgb(169, 247, 247),
    rgb(250, 222, 222),
    rgb(247, 149, 247),
    rgb(248, 166, 166)
  );
  text-align: left;
  margin-top: 30px;
  padding: 10px;
  padding-left: 50px;
  
}

#active {
  border-bottom: 2px black solid;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4), 0 8px 22px 0 rgba(0, 0, 0, 0.23);
}

#nav-link {
  font-size: x-large;
  color: black;
}

#nav-link2 {
  color: #f09433;
  color: -moz-linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  color: -webkit-linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  color: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
}

#active {
  color: #000;
  font: 1.5em "Fugaz One", cursive;
  font-weight: 300;
}

.main-images:hover{
  transition: all 10ms;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.8), 0 10px 24px 0 rgba(0, 0, 0, 0.26);
}
